import {getArea} from '@/api/areaApi.js'
import {getFreightPrompt} from '@/api/goodsApi.js'
import Provinces from '@/components/common/Provinces.vue'
export default {
	data() {
		return {
			list: [],
		  isShow: false,
		  curAddress: '',
		  prompt: '',
		  id:''
		};
	},
	components:{
		Provinces
	},
	onLoad(options) {
		this.id = options.id
	},
	onShow() {
	    getArea().then(res=>{
	      this.list = res.data
	    })
	  },
	methods: {
	    selArea(data){
	      this.curAddress = data
	      this.isShow = false
	      getFreightPrompt({
	        id: this.id,
	        province: data
	      }).then(data=>{
	        this.prompt = data.data || '无邮费项'
	      })
	    }
	  },
}
